*{
  box-sizing: border-box;
}


.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 50px;
  letter-spacing: 3px;
  color: yellow;
  text-align: center;
  padding-bottom: 3px;
  text-shadow: -2px -2px 0 blue, 2px -2px 0 blue, -2px 2px 0 blue, 2px 2px 0 blue;
}

.card{
  background-color: red;
  width: 300px;
  height: 350px;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pokemon-name{
  color: white;
  font-size: 30px;
  letter-spacing: 3px;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
}

.cards{
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
}

#scores{
  display: flex;
  gap: 20px;
  text-align: right;
  font-size: 30px;
  padding-left: 40px;
  letter-spacing: 2px;
}

#copyright{
  text-align: center;
  margin: 20px;
  font-family:'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
}